



















































import { Component, Vue } from 'vue-property-decorator';
import axios from 'axios';

@Component
export default class App extends Vue {
  // urlBase: string = 'http://13.250.27.45:8081/api/';
  // urlBase: string = 'https://application.us.zone/api/';
  urlBase: string = '/api/';
  downUrl: string = '/down';

  langSwitch = {
    ch: true,
    en: false
  };

  registerType: string = 'phone';
  onLang: any;
  onType = 0;
  onInviteCode = '****';
  getCodeString = '';

  lang: any = {
    cn: {
      phoneRegister: '手机注册',
      emailRegister: '邮箱注册',
      phone: '请输入手机号',
      email: '请输入邮箱',
      password: '请输入密码',
      paypassword: '请确认登录密码',
      code: '请输入验证码',
      getCode: '获取验证码',
      confirm: '确定',
      down: '已有账号？去下载'
    },
    en: {
      phoneRegister: 'Mobile phone registered',
      emailRegister: 'Registered mail',
      phone: 'Please enter your mobile phone number',
      email: 'Please enter email',
      password: 'Please enter password',
      paypassword: 'Please confirm the login password',
      code: 'Please enter the verification code',
      getCode: 'Get code',
      confirm: 'confirm',
      down: 'An account? To download'
    }
  };

  fromData = {
    username: '',
    code: '',
    password: '',
    re_password: '',
    invite_code: '',
    type: 2
  };

  sellp: any;

  created() {
    if(this.langSwitch.en) {
      this.onLang = this.lang.en;
    }else{
      this.onLang = this.lang.cn;
    }
    this.getCodeString = this.onLang.getCode;
    this.onInviteCode = window.location.pathname.slice(1);
  }

   langSwitchChange() {

    if(this.langSwitch.ch) {
      this.langSwitch.ch = false;
      this.langSwitch.en = true;
      this.onLang = this.lang.en;
      this.onLang.getCode = 'Get code';
      this.getCodeString = this.onLang.getCode;
    }else{
      this.langSwitch.ch = true;
      this.langSwitch.en = false;
      this.onLang = this.lang.cn;
      this.onLang.getCode = '获取验证码';
      this.getCodeString = this.onLang.getCode;
    }

    clearInterval(this.sellp);
    
  }

  switchover() {
    if(this.onType === 1) {
      this.onType = 2;
    }else{
      this.onType = 1;
    }
  }

  getCode() {
    var body = {
      username: this.fromData.username,
      type: 1
    };

    if(this.onLang.getCode === this.getCodeString) {
      if(this.onType == 1) {
        if(this.fromData.username.match('\\w+@\\w+\\.[a-z]+(\\.[a-z]+)?') === null) {
          alert('请输入正确的邮箱');
          return;
        }
      }else{
        if(this.fromData.username.match('^[0-9]*$') === null) {
          alert('请输入正确的手机号');
          return;
        }
      }

      this.onLang.getCode = 30;
      this.sellp = setInterval(() => {
        this.onLang.getCode -= 1;
        if(this.onLang.getCode == 0) {
          this.onLang.getCode = this.getCodeString;
          clearInterval(this.sellp);
        }
      }, 1000);

      axios.get(this.urlBase + 'userCode', {
        params: body,
        headers: {
          locale: this.langSwitch.ch ? 'zh-CN' : 'en'
        }
      }).then(function(res) {
        // console.log(res.data.detail);
        alert(res.data.detail);
      }).catch(function(error) {
        // console.log(error.response.data.detail);
        alert(error.response.data.detail);
      });
    }
  }

  registerSubmit() {

    if(this.onType == 1) {
      this.fromData.type = 2;
    }else{
      this.fromData.type = 1;
    }

    this.fromData.invite_code = this.onInviteCode;

    axios.request({
      method: 'post',
      url: this.urlBase + 'user',
      headers: {
        locale: this.langSwitch.ch ? 'zh-CN' : 'en'
      },
      data: this.fromData,
    }).then((res) => {
      alert('注册成功');
      window.location.href = this.downUrl;
    }).catch((error) => {
      alert(error.response.data.detail.split(';')[0]);
    });
  }

  jumpDownUrl() {
    window.location.href = this.downUrl;
  }

}
